<template>
  <BaseLayout wrapperClass="paymentInfo">
    <section class="static pt-7">
      <BContainer>
        <BRow>
          <BCol lg="3" class="d-none d-lg-block">
            <SidebarLinks :sidebarData="sidebarData" />
          </BCol>
          <BCol lg="9">
            <h1>{{ $t("payment_cancel") }}</h1>
            <div v-if="transactionId != null">
              {{ $t("transaction") }}: {{ "#" + transactionId }}
            </div>
          </BCol>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>

<script setup>
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";
import { onMounted, computed } from "vue";
import { useI18n } from "vue-i18n";
import { useHead } from "@unhead/vue";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import SidebarLinks from "@/base/core/components/SidebarLinks";

const store = useStore();
const router = useRouter();
const route = useRoute();
const { t } = useI18n();

// Compute transactionId
const transactionId = computed(() => {
  return route.query.transactionid !== undefined ? route.query.transactionid : null;
});

// Set up meta information using useHead
useHead({
  title: () => t("payment_cancel"),
  meta: [
    { name: "title", content: () => t("payment_cancel") },
    { name: "keywords", content: () => t("payment_cancel") },
    { name: "description", content: () => t("payment_cancel") },
  ],
});

// Handle breadcrumb and redirect when mounted
onMounted(() => {
  const bcrumb = { current: "success", routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
  router.push({ name: "checkout" });
});
</script>
